import { Injectable } from '@angular/core';
import { AngularFireDatabase } from '@angular/fire/compat/database';
import { AngularFireList } from '@angular/fire/compat/database';
import { IQuestion } from '../models/question';

@Injectable({
  providedIn: 'root'
})
export class QuestionService {
  private dbPath = '/questions';
  questionRef: AngularFireList<any>;

  constructor(private db: AngularFireDatabase) {
    this.questionRef = db.list(this.dbPath);
  }

  addQuestion(question: IQuestion){
    this.questionRef.push(question);
  }

  getQuestions(){
    return this.questionRef;
  }

  getQuestionById(id){
    return this.db.object(`${this.dbPath}/${id}`);
  }

  deleteQuestion(id){
    return this.questionRef.remove(id);
  }

  updateQuestion(id: string, question: IQuestion){
    return this.db.object(`${this.dbPath}/${id}`).update(question);
  }
}
